<template>
  <div>
    <el-form :inline="true">
      <el-row style="height:24px">
        <el-col :span="24" style="height:24px;text-align: left">
          <el-form-item label="지역선택">
            <law-code @code-change="codeChange"></law-code>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-divider></el-divider>
    <el-row style="height:24px">
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-office-building"></i> 아파트 <span v-if="tableData.length > 0"> : {{ tableData.length }} 건</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
            :data="tableData"
            v-loading="loading"
            element-loading-text="Loading..."
            @row-click="rowClick"
            :header-cell-style="{ background: '#000', textAlign: 'center' }"
            height="300"
            border
            style="width: 100%">
          <el-table-column
              prop="apartment_name"
              label="아파트명"
              width="280">
          </el-table-column>
          <el-table-column
              prop="dong"
              label="동">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row style="height:24px">
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-edit-outline"></i> 거래건수 <span v-if="silTrade.length > 0"> : {{ silTrade.length }} 건</span>

        <span v-if="silTrade.length > 0">&nbsp;&nbsp;<el-button :disabled="silTrade.length > 0 ? false : true"
                                                                @click="download" type="primary">Download<i
            class="el-icon-download el-icon-right"></i></el-button></span>
      </el-col>
    </el-row>
    <el-row v-if="filters1.length > 0">
      <el-col :span="24" style="height:24px;text-align: left">
        <div>
          <el-select v-model="radio1" @change="chg()" placeholder="거래구분">
            <el-option
                v-for="item in filters1"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>

          <el-select v-model="radio2" @change="chg()" placeholder="면적">
            <el-option
                v-for="item in filters2"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>

          <el-select v-model="radio3" @change="chg()" placeholder="층">
            <el-option
                v-for="item in filters3"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>

        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
            :data="silTrade"
            v-loading="loading"
            element-loading-text="Loading..."
            :header-cell-style="{ background: '#000', textAlign: 'center' }"
            height="300"
            border
            style="width: 100%">
          <el-table-column
              prop="순번"
              label="순번"
              width="80">
          </el-table-column>
          <el-table-column
              prop="거래구분"
              label="거래구분"
              width="100">
          </el-table-column>
          <el-table-column
              prop="아파트명"
              label="아파트명"
              width="280">
          </el-table-column>
          <el-table-column
              prop="면적"
              label="면적"
              width="80">
          </el-table-column>
          <el-table-column
              prop="층"
              label="층"
              width="80">
          </el-table-column>
          <el-table-column
              prop="건축년도"
              label="건축년도"
              width="80">
          </el-table-column>
          <el-table-column
              prop="거래금액"
              label="거래금액"
              width="80">
          </el-table-column>
          <el-table-column
              prop="거래일자"
              label="거래일자"
              width="100">
          </el-table-column>
          <el-table-column
              prop="보증금"
              label="보증금"
              width="80">
          </el-table-column>
          <el-table-column
              prop="월세"
              label="월세"
              width="80">
          </el-table-column>
          <el-table-column
              prop="동"
              label="동"
              width="80">
          </el-table-column>
          <el-table-column
              prop="지번"
              label="지번"
              width="80">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-data-line"></i> Chart
      </el-col>
    </el-row>
    <el-row v-if="visible == true && chartData.length > 0">
      <el-col :span="24">
        <trade-chart :chartData="chartData" :chartCategory="chartCate"></trade-chart>
      </el-col>
    </el-row>
  </div>


</template>
<script>
import LawCode from "@/components/LawCode";
import TradeChart from "@/components/TradeChart";

export default {
  components: {
    LawCode, TradeChart
  },

  created() {
    this.getData();

    this.$axios.interceptors.request.use((config) => {
      this.loading = true;
      return config;
    }, (error) => {
      this.loading = false;
      return Promise.reject(error);
    });

    this.$axios.interceptors.response.use((response) => {
      this.loading = false;
      return response;
    }, (error) => {
      this.loading = false;
      return Promise.reject(error);
    });

  },
  data() {
    return {
      rows: [],
      tableData: [],
      silTrade: [],
      trades: [],
      loading: false,
      visible : false,
      dealTypes: [],
      areas: [],
      floors: [],
      radio1: "",
      radio2: "",
      radio3: "",
      filters1: [],
      filters2: [],
      filters3: [],
      chartData: [],
      chartCate: [],
    }
  },
  computed: {
    disabled() {
      return this.silTrade.length > 0 ? true : false;
    }
  },
  methods: {
    codeChange(lastCate) {
      let apts = this.rows.filter(row => row[0].substring(0, 10) === lastCate);
      let tableData = [];
      apts.map(e => tableData.push({
        "acode": e[0],
        "apartment_name": e[1],
        "dong": e[2],
      }))

      this.tableData = tableData;
    },
    getData() {
      this.$axios.get('/apt/' + 'apt_code.csv', {params: {}})
          .then((response) => {
            let rows = [];
            let r = response.data.replace(/["]+/g, "").split("\n");
            r.map(e => rows.push(e.split(",")))
            this.rows = rows;
            this.loading = false;
          });
    },
    rowClick(row) {
      this.$axios.get('/apt/' + row.acode + '.csv', {params: {}})
          .then((response) => {
            let data = response.data.replace(/["]+/g, "").split("\n");
            let rows = [];
            data.map(e => e.length > 0 ? rows.push(e.split(",")) : 1 == 1)
            let silTrade = [];
            let k = 0;
            rows.map(e => silTrade.push({
              순번: ++k,
              아파트코드: e[0],
              아파트명: e[1],
              면적: e[2],
              건축년도: e[3],
              취소일: e[4],
              취소여부: e[5],
              거래금액: e[6],
              거래일자: e[9] + "-" + this.dateType(e[8]) + "-" + this.dateType(e[7]),
              보증금: e[10],
              동: e[11],
              층: e[12],
              지번: e[13],
              법정코드: e[14],
              월세: e[15],
              거래구분: this.dealType(e[16], e[15]),
            }))
            this.dealTypes = this.groupKey(silTrade, "거래구분");
            this.areas = this.groupKey(silTrade, "면적");
            this.floors = this.groupKey(silTrade, "층");
            this.silTrade = silTrade;
            this.trades = silTrade;

            this.filters1 = Object.keys(this.dealTypes)
            this.filters2 = Object.keys(this.areas)
            this.filters3 = Object.keys(this.floors)

            this.filters1.unshift("전체");
            this.filters2.unshift("전체");
            this.filters3.unshift("전체");

            this.createChart();

          });
    },
    dealType(type, rent) {
      if (type === 'N') {
        return "매매";
      } else {
        if (parseInt(rent) > 0) {
          return "월세";
        } else {
          return "전세"
        }
      }
    },
    chg() {
      this.silTrade = this.trades;

      if (this.radio1 != "" && this.radio1 != "전체") {
        this.silTrade = this.silTrade.filter(e => e["거래구분"] == this.radio1)
      } else {
        this.silTrade = this.silTrade.filter(e => e["거래구분"].length > 0)
      }

      if (this.radio2 != "" && this.radio2 != "전체") {
        this.silTrade = this.silTrade.filter(e => e["면적"] == this.radio2)
      } else {
        this.silTrade = this.silTrade.filter(e => e["면적"].length > 0)
      }

      if (this.radio3 != "" && this.radio3 != "전체") {
        this.silTrade = this.silTrade.filter(e => e["층"] == this.radio3)
      } else {
        this.silTrade = this.silTrade.filter(e => e["층"].length > 0)
      }
      this.createChart();
    },
    download() {
      this.excelDownload(this.silTrade, "거래결과", this.silTrade[0]["아파트명"]);
    },
    createChart() {
      this.visible = false;
      this.chartCate = [];
      this.chartData = [];

      this.chartCate.push("매매");
      this.chartCate.push("전세");
      this.chartCate.push("월세");

      let dt = [];
      let dt2 = [];
      let dt3 = [];

      this.silTrade.map(ele => {
        if(ele["거래구분"] == "매매") {
          dt.push([ele["거래일자"], ele["거래금액"], ele["층"], ele["면적"], ele["거래구분"], ele["보증금"], ele["월세"]])
        } else if(ele["거래구분"] == "전세") {
          dt2.push([ele["거래일자"], ele["보증금"], ele["층"], ele["면적"], ele["거래구분"], ele["보증금"], ele["월세"]])
        } else {
          dt3.push([ele["거래일자"], ele["월세"], ele["층"], ele["면적"], ele["거래구분"], ele["보증금"], ele["월세"]])
        }
      });


      let chartData = {
        name: "매매",
        symbolSize: 6,
        data: dt,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      chartData = {
        name: "전세",
        symbolSize: 6,
        data: dt2,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      chartData = {
        name: "월세",
        symbolSize: 6,
        data: dt3,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      this.visible = true;
    }
  }
}
</script>