<template>
  <div>
    <el-row style="height:24px">
      <el-col :span="24" style="height:24px;text-align: left">
        <el-button :disabled="silTrade.length > 0 ? false : true"
                   @click="download" type="primary" size="small">거래내역 엑셀 다운로드<i
            class="el-icon-download el-icon-right"></i></el-button>

        <el-button :disabled="silTrade.length > 0 ? false : true"
                   @click="downloadChart" type="primary" size="small">차트 이미지 다운로드<i
            class="el-icon-download el-icon-right"></i></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="6" style="font-weight: bold;text-align: left;" >
        <i class="el-icon-location-information"></i> 지역
      </el-col>
      <el-col :span="18" style="text-align: left">
        <span v-if="addr[1]">{{ addr[1] }}</span>
        <span v-if="addr[2]"> <i class="el-icon-arrow-right"></i> {{ addr[2] }}</span>
        <span v-if="addr[3]"> <i class="el-icon-arrow-right"></i> {{ addr[3] }}</span>
        <span v-if="addr[4]"> <i class="el-icon-arrow-right"></i> {{ addr[4] }}</span>
        <span v-if="addr[5]"> <i class="el-icon-arrow-right"></i> {{ addr[5] }}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="6" style="font-weight: bold;text-align: left">
        <i class="el-icon-office-building"></i> 아파트명
      </el-col>
      <el-col :span="18" style="text-align: left">
        <span v-if="apt[1]">{{ apt[1] }}</span>
      </el-col>
    </el-row>
    <el-row style="height:24px">
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-edit-outline"></i> 거래내역 <span v-if="silTrade.length > 0"> : {{ silTrade.length }} 건</span>
      </el-col>
    </el-row>
    <el-row v-if="filters1.length > 0">
      <el-col :span="6" style="font-weight: bold;text-align: left">
        <i class="el-icon-data-analysis"></i> 거래구분
      </el-col>
      <el-col :span="18" style="height:24px;text-align: left;font-weight: bold">
        <div>
          <el-select v-model="radio1" @change="chg()" placeholder="거래구분">
            <el-option
                v-for="item in filters1"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row v-if="filters2.length > 0">
      <el-col :span="6" style="font-weight: bold;text-align: left">
        <i class="el-icon-data-analysis"></i> 면적
      </el-col>
      <el-col :span="18" style="height:24px;text-align: left;font-weight: bold">
        <div>
          <el-select v-model="radio2" @change="chg()" placeholder="면적">
            <el-option
                v-for="item in filters2"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row v-if="filters3.length > 0">
      <el-col :span="6" style="font-weight: bold;text-align: left">
        <i class="el-icon-data-analysis"></i> 층
      </el-col>
      <el-col :span="16" style="height:24px;text-align: left;font-weight: bold">
        <div>
          <el-select v-model="radio3" @change="chg()" placeholder="층">
            <el-option
                v-for="item in filters3"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
            :data="silTrade"
            :header-cell-style="{ background: '#000', textAlign: 'center' }"
            border
            style="width: 100%">
          <el-table-column
              prop="순번"
              label="순번"
              width="50">
          </el-table-column>
          <el-table-column
              prop="거래구분"
              label="거래구분">
          </el-table-column>
          <el-table-column
              prop="면적"
              label="면적">
          </el-table-column>
          <el-table-column
              prop="층"
              label="층"
              width="40">
          </el-table-column>
          <el-table-column
              prop="건축년도"
              label="건축년도">
          </el-table-column>
          <el-table-column
              prop="거래금액"
              label="거래금액">
          </el-table-column>
          <el-table-column
              prop="거래일자"
              label="거래일자"
              width="100">
          </el-table-column>
          <el-table-column
              prop="보증금"
              label="보증금">
          </el-table-column>
          <el-table-column
              prop="월세"
              label="월세">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" style="height:24px;text-align: left">
        <i class="el-icon-data-line"></i> Chart
      </el-col>
    </el-row>
    <el-row v-if="chartData.length > 0">
      <el-col :span="24">
        <trade-chart ref="tradeChart" :chartData="chartData" :chartCategory="chartCate"></trade-chart>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import TradeChart from "@/components/TradeChart";

export default {
  name: "AptView",
  components: {
    TradeChart
  },
  created() {
    this.load = this.$loading({
      lock: true,
      text: 'Loading',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    });
    this.getInfo();
  },
  props: {
    acode: {
      type: String,
    },
  },
  data() {
    return {
      addr: [],
      apt: [],
      silTrade: [],
      load: null,
      dealTypes: [],
      areas: [],
      floors: [],
      radio1: "",
      radio2: "",
      radio3: "",
      filters1: [],
      filters2: [],
      filters3: [],
      chartData: [],
      chartCate: [],
    }
  },
  methods: {
    getInfo() {
      this.$axios.get('/apt/' + 'law_code.csv', {params: {}})
          .then((response) => {
            let rows = [];
            let r = response.data.replace(/["]+/g, "").split("\n");
            r.map(e => rows.push(e.split(",")))
            this.addr = rows[rows.findIndex(e => e[0] === this.acode.substring(0, 10))];

            this.$axios.get('/apt/' + 'apt_code.csv', {params: {}})
                .then((response) => {
                  let rows = [];
                  let r = response.data.replace(/["]+/g, "").split("\n");
                  r.map(e => rows.push(e.split(",")))
                  this.apt = rows[rows.findIndex(e => e[0] === this.acode)];

                  this.$axios.get('/apt/' + this.acode + '.csv', {params: {}})
                      .then((response) => {
                        let data = response.data.replace(/["]+/g, "").split("\n");
                        let rows = [];
                        data.map(e => e.length > 0 ? rows.push(e.split(",")) : 1 == 1)
                        let silTrade = [];
                        let k = 0;
                        rows.map(e => silTrade.push({
                          순번: ++k,
                          아파트코드: e[0],
                          아파트명: e[1],
                          면적: e[2],
                          건축년도: e[3],
                          취소일: e[4],
                          취소여부: e[5],
                          거래금액: e[6],
                          거래일자: e[9] + "-" + this.dateType(e[8]) + "-" + this.dateType(e[7]),
                          보증금: e[10],
                          동: e[11],
                          층: e[12],
                          지번: e[13],
                          법정코드: e[14],
                          월세: e[15],
                          거래구분: this.dealType(e[16], e[15]),
                        }))

                        silTrade = silTrade.reverse();
                        this.dealTypes = this.groupKey(silTrade, "거래구분");
                        this.areas = this.groupKey(silTrade, "면적");
                        this.floors = this.groupKey(silTrade, "층");
                        this.silTrade = silTrade;
                        this.trades = silTrade;

                        this.filters1 = Object.keys(this.dealTypes)
                        this.filters2 = Object.keys(this.areas)
                        this.filters3 = Object.keys(this.floors)

                        this.filters1.unshift("전체");
                        this.filters2.unshift("전체");
                        this.filters3.unshift("전체");


                        this.createChart();

                      });


                });
          });
    },
    dealType(type, rent) {
      if (type === 'N') {
        return "매매";
      } else {
        if (parseInt(rent) > 0) {
          return "월세";
        } else {
          return "전세"
        }
      }
    },
    download() {
      this.excelDownload(this.silTrade, "거래결과", this.silTrade[0]["아파트명"]);
    },
    downloadChart() {
      this.$refs.tradeChart.downloadChart(this.silTrade[0]["아파트명"]);
    },
    chg() {
      this.silTrade = this.trades;

      if (this.radio1 != "" && this.radio1 != "전체") {
        this.silTrade = this.silTrade.filter(e => e["거래구분"] == this.radio1)
      } else {
        this.silTrade = this.silTrade.filter(e => e["거래구분"].length > 0)
      }

      if (this.radio2 != "" && this.radio2 != "전체") {
        this.silTrade = this.silTrade.filter(e => e["면적"] == this.radio2)
      } else {
        this.silTrade = this.silTrade.filter(e => e["면적"].length > 0)
      }

      if (this.radio3 != "" && this.radio3 != "전체") {
        this.silTrade = this.silTrade.filter(e => e["층"] == this.radio3)
      } else {
        this.silTrade = this.silTrade.filter(e => e["층"].length > 0)
      }
      this.createChart();
    },
    createChart() {
      this.chartCate = [];
      this.chartData = [];

      this.chartCate.push("매매");
      this.chartCate.push("전세");
      this.chartCate.push("월세");

      let dt = [];
      let dt2 = [];
      let dt3 = [];

      this.silTrade.map(ele => {
        if (ele["거래구분"] == "매매") {
          dt.push([ele["거래일자"], ele["거래금액"], ele["층"], ele["면적"], ele["거래구분"], ele["보증금"], ele["월세"]])
        } else if (ele["거래구분"] == "전세") {
          dt2.push([ele["거래일자"], ele["보증금"], ele["층"], ele["면적"], ele["거래구분"], ele["보증금"], ele["월세"]])
        } else {
          dt3.push([ele["거래일자"], ele["월세"], ele["층"], ele["면적"], ele["거래구분"], ele["보증금"], ele["월세"]])
        }
      });


      let chartData = {
        name: "매매",
        symbolSize: 6,
        data: dt,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      chartData = {
        name: "전세",
        symbolSize: 6,
        data: dt2,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      chartData = {
        name: "월세",
        symbolSize: 6,
        data: dt3,
        markPoint: {
          symbol: 'pin',
          data: [{type: 'max', name: 'max'},],
          itemStyle: {
            color: '#3A1079'
          },
        },
        type: 'scatter'
      };

      this.chartData.push(chartData);

      if (this.load) this.load.close();
    },
  }
}
</script>
<style scoped></style>