import Vue from 'vue'
import Vuex from 'vuex'

import locale from 'element-ui/lib/locale/lang/ko'
import Element from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router'
import '@fortawesome/fontawesome-free/js/all.js'
import axios from "axios";
import VueClipboard from 'vue-clipboard2'
import XLSX from 'xlsx'
import App from './App.vue'

axios.defaults.baseURL = 'https://onehit9.github.io'
Vue.prototype.$axios = axios;
Vue.prototype.$xlsx = XLSX;
Vue.config.productionTip = false
Vue.use(Element, {locale},{ size: 'small', zIndex: 3000 });
Vue.use(VueClipboard);
Vue.use(Vuex);

Vue.mixin({
  methods: {
    excelDownload(item,sheet_nm,file_nm) {
      let dataWS = this.$xlsx.utils.json_to_sheet(item);
      let wb = this.$xlsx.utils.book_new();
      this.$xlsx.utils.book_append_sheet(wb, dataWS , sheet_nm);
      this.$xlsx.writeFile(wb, file_nm + ".xlsx");
    },
    groupKey(data, key) {
      return data.reduce(function (carry, el) {
        let group = el[key];

        if (carry[group] === undefined) {
          carry[group] = []
        }

        return carry
      }, {})
    },
    groupBy(data, key) {
      return data.reduce(function (carry, el) {
        let group = el[key];

        if (carry[group] === undefined) {
          carry[group] = []
        }

        carry[group].push(el)
        return carry
      }, {})
    },
    dateType(num) {
      return parseInt(num) < 10 ? ("0" + num) : num;
    },
  }
})
new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
